.select__button {
  display: flex;
  background-color: transparent;
  border: none;
  padding: 0;
  gap: 10px;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
  color: rgba($color-default-white, 0.7);
  line-height: $lh-xxsmall;
  letter-spacing: $ls-light;
  margin: 0;

  svg {
    width: 12px;
    height: 12px;
    fill: none;
    stroke: rgba($color-default-white, 0.7);
    transition: $trans-default;
  }

  @include hover-focus {
    outline: none;
    color: rgba($color-default-white, 1);

    svg {
      stroke: rgba($color-default-white, 1);
    }

    @include active {
      color: rgba($color-default-white, 1);

      svg {
        stroke: rgba($color-default-white, 1);
      }
    }
  }
}

.select-wrapper.is-active {
  & .select__list {
    margin-top: 12px;
    padding: 0 0 0 16px;
    display: block;
    visibility: visible;
    transition: all $trans-default;
  }

  & .select__button {
    color: rgba($color-default-white, 1);

    & svg {
      stroke: rgba($color-default-white, 1);
      transform: rotate(180deg);
      transition: $trans-default;
    }
  }
}

.select__list {
  margin: 0;
  padding: 0;
  list-style: none;
  visibility: hidden;
}

.select__item {
  padding-bottom: 4px;

  &:last-child {
    padding-bottom: 0;
  }
}

.select__link {
  outline: none;
  text-transform: capitalize;
  letter-spacing: 0;
  line-height: $lh-xxsmall;
  color: rgba($color-default-white, 0.7);

  @include hover-focus {
    color: rgba($color-default-white, 1);
  }

  @include active {
    color: rgba($color-default-white, 1);
  }
}
