      border-top-right-radius: 4px;
    }

    &:last-child {
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      border-bottom: none;
    }

    &.leaflet-disabled {
      cursor: default;
      background-color: #f4f4f4;
      color: #bbb;
    }
  }
}

.leaflet-bar a,
